<template>
  <section class="swipers" id="swipe">
    <div class="container">
      <h2 class="swipers__title">Галерея</h2>
      <div class="swipers__line">
        <span>
          <!-- <img src="@/assets/images/photo1.jpg" alt="" /> -->
          <fa :icon="['far', 'heart']" class="swipers__heart"/>
        </span>
      </div>
      <Swiper
        class="swipers__slide"
        :modules="modules"
        :slides-per-view="1"
        navigation
        :effect="'cube'"
        :grabCursor="true"
        :cubeEffect="{
          shadow: true,
          slideShadows: true,
          shadowOffset: 20,
          shadowScale: 0.94,
        }"
      >
        <SwiperSlide
          class="swipers__slide-item"
          v-for="(img,index) in swipePhotos"
          :key="img.scr"
        >
          <img
            class="swipers__slide-img" :class="{active: index == 5 || index == 4}"
            :src="img.src"
            alt=""
          />
        </SwiperSlide>
      </Swiper>
    </div>
    <div v-for="image in swipePhotos" :key="image.id">
    
    </div>
  </section>
</template>

<script>
import { Swiper, SwiperSlide } from "swiper/vue";
import { Navigation, EffectCube } from "swiper";
import "swiper/scss";
import "swiper/scss/navigation";
import "swiper/css/effect-cube";
export default {
  components: {
    Swiper,
    SwiperSlide,
  },
  data() {
    return {
      modules: [Navigation, EffectCube],
      swipePhotos: [
        { src: require('../assets/images/swipe1.jpg')},
        { src: require('../assets/images/swipe2.jpg')},
        { src: require('../assets/images/swipe3.jpg')},
        { src: require('../assets/images/swipe4.jpg')},
        { src: require('../assets/images/swipe5.jpg')},
        { src: require('../assets/images/swipe6.jpg')},
        { src: require('../assets/images/swipe7.jpg')},
        { src: require('../assets/images/swipe8.jpg')},
        { src: require('../assets/images/swipe9.jpg')},
      ],
    };
  },

};
</script>

