<template>
  <section class="time">
    <div class="container">
      <div class="time__top">
        <div class="time__top-photo">
          <img src="@/assets/images/me.jpg" alt="" />
          <span>Евгений</span>
        </div>
        <div class="time__top-photo">
          <img src="@/assets/images/she.jpg" alt="" />
          <span>Светлана</span>
        </div>
      </div>
      <div class="time__date">
        <h2 class="time__date-title">{{loaded ? 'Мы станем семьей через' : 'Мы уже семья'}}</h2>
        <div class="time__items" v-if="loaded">
          <div class="time__item time__item-days">
            {{ displayDays }}
            <span>дней</span>
          </div>
          <div class="time__item time__item-hours">
            {{ displayHours }}
            <span>часов</span>
          </div>
          <div class="time__item time__item-minutes">
            {{ displayMinutes }}
            <span>минут</span>
          </div>
          <div class="time__item time__item-second">
            {{ displaySeconds }}
            <span>секунд</span>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      displayDays: 0,
      displayHours: 0,
      displayMinutes: 0,
      displaySeconds: 0,
      loaded: false
    };
  },
  computed: {
    seconds: () => 1000,
    minutes() {
      return this.seconds * 60;
    },
    hours() {
      return this.minutes * 60;
    },
    days() {
      return this.hours * 24;
    },
  },
  mounted() {
    this.showRemaning()
  },
  methods: {
    showRemaning() {
      const timer = setInterval(() => {
        const now = new Date();
        const end = new Date(2023, 3, 8, 18, 0, 0, 0);
        const distance = end.getTime() - now.getTime()
        
        if(distance < 0){
          clearInterval(timer)
          return
        }
        const days = Math.floor((distance / this.days))
        const hours = Math.floor((distance % this.days) / this.hours)
        const minutes = Math.floor((distance % this.hours) / this.minutes)
        const seconds = Math.floor((distance % this.minutes) / this.seconds)
        
        this.displayMinutes = minutes < 10 ? '0' + minutes : minutes
        this.displaySeconds = seconds < 10 ? '0' + seconds : seconds
        this.displayHours = hours < 10 ? '0' + hours : hours
        this.displayDays = days < 10 ? '0' + days : days
        this.loaded = true
      }, 1000);
    },
  },
};
</script>

